function sliderAdvantages() {
    var swiper = new Swiper('.advantages .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 15,
        navigation: {
            nextEl: ".advantages .swiper-button-next",
            prevEl: ".advantages .swiper-button-prev",
        },
    })
}

function sliderSpecialists() {
    var swiper = new Swiper('.specialists .mySwiper', {
        slidesPerView: 3,
        spaceBetween: 32,
        navigation: {
            nextEl: ".specialists .swiper-button-next",
            prevEl: ".specialists .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 16,
            },
            500: {
                slidesPerView: 2,
                spaceBetween: 16,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 16,
            },
            1400: {
                slidesPerView: 3,
                spaceBetween: 32,
            },
        }
    })
}



$(document).ready(function () {

    sliderAdvantages();
    sliderSpecialists();

    //Accordion
    $(".description__item-show").click((function() {
        $(this).toggleClass("description__item-show--active")
        $(this).siblings(".description__item-hidden").slideToggle()
    }));

    //All-text
    $('.specialist-card__btn').click(function () {
        $(this).toggleClass('specialist-card__btn--active')
        $(this).parents('.specialist-card').toggleClass('specialist-card--active')
    })

//Zoom
    function updateZoomVariable() {
        // Определяем ширину окна браузера
        const screenWidth = window.innerWidth;
        let zoomValue;

        if (screenWidth > 1920) {
            // Вычисляем zoom для экранов шире 1920px
            zoomValue = (screenWidth / 1920).toFixed(2);
        } else {
            // Для экранов 1920px или уже, устанавливаем zoom равным 1
            zoomValue = 1;
        }

        // Устанавливаем CSS переменную --zoom для корневого элемента (HTML)
        document.documentElement.style.setProperty('--zoom', zoomValue);
    }

// Вызываем функцию первоначально, чтобы установить переменную при загрузке страницы
    updateZoomVariable();

// Добавляем слушатель событий, чтобы обновлять переменную при изменении размера окна
    window.addEventListener('resize', updateZoomVariable);    


    //Animation
    gsap.registerPlugin(ScrollTrigger);

    //main-section
    const tl = gsap.timeline({defaults: {duration: 0.7}})
    tl.from('.main-section__text', {opacity: 0, x: -150})
        .from('.main-section__left h1', {opacity: 0, x: -150})
        .from('.main-section__subtitle', {opacity: 0, x: -150})
        .from('.main-section__group', {opacity: 0, x: -150})

    const tlImg = gsap.timeline({defaults: {duration: 1.5}, repeat: -1, yoyo: true})
    tlImg.to('.main-section__img', {y: -8})
        .to('.main-section__img', {y: 8})

    gsap.from('.main-section__text2', {duration: 1, opacity: 0, x: 150})

    //advantages
    const tl2 = gsap.timeline({
        defaults: {
            duration: 0.7
        },
        scrollTrigger: {
            trigger: '.advantages__left',
            start: 'top 70%',
        }
    })
    tl2.from('.advantages__left h2', {
        opacity: 0,
        x: -150
    }).from('.advantages__text', {
        opacity: 0,
        x: -150
    })

    const tl3 = gsap.timeline({
        defaults: {
            duration: 0.7
        },
        scrollTrigger: {
            trigger: '.advantages__items',
            start: 'top 70%',
        }
    })
    tl3.from('.advantages__item:nth-child(1)', {
        opacity: 0,
        y: 100
    }).from('.advantages__item:nth-child(2)', {
        opacity: 0,
        y: 100
    }).from('.advantages__item:nth-child(3)', {
        opacity: 0,
        y: 100
    })

    //requests
    gsap.from('.requests h2', {
        opacity: 0,
        y: 80,
        duration: 0.7,
        scrollTrigger: {
            trigger: '.requests h2',
            start: 'top 70%',
        }
    })


    const tl4 = gsap.timeline({
        defaults: {
            duration: 1,
            repeat: -1,
            repeatDelay: 3,
        },
        scrollTrigger: {
            trigger: '.requests__items',
            start: 'top 70%',
        }
    })
    tl4.to('.requests__item-img', {
        rotate: 0
    }).to('.requests__item-img', {
        rotate: 7
    }).to('.requests__item-img', {
        rotate: 0
    })

    gsap.from('.requests__group', {
        opacity: 0,
        x: -150,
        duration: 1,
        scrollTrigger: {
            trigger: '.requests__group',
            start: 'top 70%',
        }
    })

    //info2
    gsap.fromTo('.info2', { opacity: '0' }, {
        opacity: '1',
        scrollTrigger: {
            trigger: '.info2',
            start: 'top 80%',
            scrub: true,
        }
    })

    //services
    gsap.fromTo('.services', { opacity: '0' }, {
        opacity: '1',
        scrollTrigger: {
            trigger: '.services',
            start: 'top 20%',
            scrub: true,
        }
    })

    //employee
    const tl5 = gsap.timeline({
        defaults: {
            duration: 0.7
        },
        scrollTrigger: {
            trigger: '.employee__left',
            start: 'center',
        }
    })
    tl5.from('.employee__name', {
        opacity: 0,
        x: -150
    }).from('.employee__left .employee__text', {
        opacity: 0,
        x: -150
    })

    gsap.from('.employee__right', {
        opacity: 0,
        x: 150,
        duration: 1,
        scrollTrigger: {
            trigger: '.employee__right',
            start: 'center',
        }
    })

    //description
    gsap.fromTo('.description', { opacity: '0' }, {
        opacity: '1',
        scrollTrigger: {
            trigger: '.description',
            start: 'top 20%',
            scrub: true,
        }
    })

    //advantages2
    const tl6 = gsap.timeline({
        defaults: {
            duration: 0.7
        },
        scrollTrigger: {
            trigger: '.advantages2 h2',
            start: 'center',
        }
    })
    tl6.from('.advantages2 h2', {
        opacity: 0,
        y: 80,
    }).from('.advantages2 p', {
        opacity: 0,
        y: 80,
    })

    gsap.from('.advantages2__left', {
        opacity: 0,
        x: -150,
        duration: 0.7,
        scrollTrigger: {
            trigger: '.advantages2__left',
            start: 'center',
        }
    })

    gsap.from('.advantages2__right', {
        opacity: 0,
        x: 150,
        duration: 0.7,
        scrollTrigger: {
            trigger: '.advantages2__right'                                ,
            start: 'center',
        }
    })

    gsap.from('.advantages2__group2', {
        opacity: 0,
        x: -150,
        duration: 1,
        scrollTrigger: {
            trigger: '.advantages2__group2'                                ,
            start: 'top 70%',
        }
    })

    //specialists
    gsap.fromTo('.specialists', { opacity: '0' }, {
        opacity: '1',
        scrollTrigger: {
            trigger: '.specialists',
            start: 'top 20%',
            scrub: true,
        }
    })

    //connection
    gsap.from('.connection__left', {
        opacity: 0,
        x: -150,
        duration: 1,
        scrollTrigger: {
            trigger: '.connection__left',
            start: 'center',
        }
    })

    gsap.from('.connection__right .form-wrap', {
        opacity: 0,
        x: 150,
        duration: 1,
        scrollTrigger: {
            trigger: '.connection__right .form-wrap',
            start: 'center',
        }
    })

})


  